// src/components/Sidebar.js
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Nav } from "react-bootstrap";

const Sidebar = () => {
  const [hoveredLink, setHoveredLink] = useState(null);

  const sidebarStyle = {
    width: "240px",
    height: "100vh",
    backgroundColor: "#f9f9f9",
    display: "flex",
    flexDirection: "column",
    padding: "20px",
  };

  const navLinkStyle = {
    color: "#333",
    padding: "10px",
    borderRadius: "5px",
    transition: "background 0.3s ease",
  };

  const navLinkHoverStyle = {
    backgroundColor: "#e9e9e9",
  };

  const handleMouseEnter = (link) => {
    setHoveredLink(link);
  };

  const handleMouseLeave = () => {
    setHoveredLink(null);
  };

  return (
    <div style={sidebarStyle}>
      <Nav defaultActiveKey="/home" className="flex-column">
        <Nav.Link
          as={Link}
          to="/flexdesign"
          style={
            hoveredLink === "flexdesign"
              ? { ...navLinkStyle, ...navLinkHoverStyle }
              : navLinkStyle
          }
          onMouseEnter={() => handleMouseEnter("flexdesign")}
          onMouseLeave={handleMouseLeave}
        >
          Diseo Flexin Simple
        </Nav.Link>
        <Nav.Link
          as={Link}
          to="/rebarembed"
          style={
            hoveredLink === "rebarembed"
              ? { ...navLinkStyle, ...navLinkHoverStyle }
              : navLinkStyle
          }
          onMouseEnter={() => handleMouseEnter("rebarembed")}
          onMouseLeave={handleMouseLeave}
        >
          Largo de Desarrollo Barras
        </Nav.Link>
        <Nav.Link
          as={Link}
          to="/spectreplot"
          style={
            hoveredLink === "spectreplot"
              ? { ...navLinkStyle, ...navLinkHoverStyle }
              : navLinkStyle
          }
          onMouseEnter={() => handleMouseEnter("spectreplot")}
          onMouseLeave={handleMouseLeave}
        >
          Espectro de diseo NCh2369 2003 y 2023
        </Nav.Link>
        <Nav.Link
          as={Link}
          to="/foundation1d"
          style={
            hoveredLink === "foundation1d"
              ? { ...navLinkStyle, ...navLinkHoverStyle }
              : navLinkStyle
          }
          onMouseEnter={() => handleMouseEnter("foundation1d")}
          onMouseLeave={handleMouseLeave}
        >
          Fundacin 1D
        </Nav.Link>
        {/* <Nav.Link
          as={Link}
          to="/app4"
          style={
            hoveredLink === "app4"
              ? { ...navLinkStyle, ...navLinkHoverStyle }
              : navLinkStyle
          }
          onMouseEnter={() => handleMouseEnter("app4")}
          onMouseLeave={handleMouseLeave}
        >
          Two Js Try
        </Nav.Link> */}
      </Nav>
    </div>
  );
};

export default Sidebar;
